.main-head {
  min-height: calc(100vh - 94px);
  padding: 40px 0;
  position: relative;
  overflow: hidden;

  @include media("tb") {
    padding: 30px 0;
  }

  @include media("mb") {
    padding: 25px 0;
  }

  &__title,
  &__text,
  &__footer {
    max-width: 430px;

    @include media("tb") {
      max-width: 350px;
    }

    @include media("mb") {
      max-width: none;
    }
  }

  &__picture {
    position: absolute;
    z-index: -1;
    pointer-events: none;
    top: 50%;
    left: calc(50% + 355px);
    transform: translate(-50%, -50%);
    animation: main-head-img 2.5s ease-in-out 0s infinite;
    height: 847px;
    width: 944px;

    @include media("mtb") {
      left: calc(50% + 247px);
      width: 547px;
      height: auto;
    }

    @include media("md") {
      left: calc(50% + 355px);
    }

    @include media("mb") {
      width: 100%;
      animation: main-head-img-mobile 2.5s ease-in-out 0s infinite;
      position: static;
      margin-top: 24px;
    }

    &-img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
  }

  &__text {
    margin-top: 30px;

    @include media("tb") {
      margin-top: 23px;
    }
  }

  &__footer {
    margin-top: 45px;

    @include media("tb") {
      margin-top: 31px;
    }

    @include media("lt") {
      display: block;
    }

    &-link {
      flex: 0 0 210px;
      margin-right: 20px;

      @include media("tb") {
        flex: 0 0 177px;
      }

      @include media("mb") {
        flex: 0 0 168px;
      }

      @include media("lt") {
        margin-right: 0;
      }
    }

    &-text {
      font-weight: normal;
      font-size: 13px;
      line-height: 19px;

      @include media("mb") {
        font-size: 12px;
        line-height: 143.4%;
      }

      @include media("lt") {
        margin-top: 10px;
        text-align: center;
      }

      a {
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit;
      }
    }
  }
}

.areas-courses {
  h1 {
    font-size: 36px;
    font-weight: 700;
    line-height: 68px;
    margin-top: 20px;
    margin-bottom: 20px;
    color: rgba(15, 23, 39, 0.85);
  }

  h2 {
    font-size: 28px;
    font-weight: 600;
    margin: 32px 0 16px 0;
  }

  h3 {
    margin-bottom: 16px;
  }

  p {
    font-weight: 400;
    line-height: 1.875;
    text-align: justify;
    margin-bottom: 1rem;
  }

  a {
    color: #000;
    transition: color 0.3s ease;

    &:hover {
      color: #3e3e3e;
    }
  }

  span {
    font-weight: 400;
  }

  &__subtitle {
    font-weight: 400;
    color: #000000;
    line-height: 1.25;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    flex: 0 0 auto;
    margin-bottom: 30px;

    @include for-1100-px {
      flex-direction: column;
    }
  }

  &__item-directions {
    flex-basis: 66.66666667%;
    max-width: 66.66666667%;
    margin-top: 16px;

    @include for-1100-px {
      flex-basis: 100%;
      max-width: 100%;
    }
  }

  &__item-title {
    position: relative;

    &::before {
      content: "\27A4";
      color: #0075ff;
    }
  }

  &__item-link {
    text-decoration: underline;
    font-size: 24px;

    &:hover {
      text-decoration: underline;

      .areas-courses__item-link-text {
        &::after {
          width: 30%;
        }
      }
    }
  }

  &__item-link-text {
    position: relative;

    &::after {
      display: block;
      left: 0;
      width: 0;
      height: 2px;
      background-color: #0075ff;
      content: "";
      transition: width 0.3s ease-out;
    }
  }

  &__item-img {
    flex-basis: 33.33333333%;
    max-width: 33.33333333%;
    display: flex;
    justify-content: center;
    align-items: center;

    @include media("tb") {
      display: block;
      margin: 0 auto;
      width: 50%;
      max-width: 50%;
    }

    @include media("md") {
      width: 70%;
      max-width: 70%;
    }

    @include media("mb") {
      width: 90%;
      max-width: 90%;
    }

    img {
      height: auto;
      max-width: 100%;
    }

    @include for-1100-px {
      max-width: 50%;
      flex-basis: 50%;
      align-self: center;
    }
  }

  &__text {
    line-height: 1.875;
    width: 50%;
    text-align: justify;
    margin-right: 15px;

    p {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    @include for-1100-px {
      width: 100%;
      margin-right: 0;
    }
  }

  &__text-link {
    transition: color 0.3s ease;

    &:hover {
      color: #3e3e3e;
    }
  }

  &__img {
    width: 50%;
    object-fit: contain;

    @include media("tb") {
      display: block;
      margin: 0 auto;
    }

    @include media("md") {
      width: 70%;
    }

    @include media("mb") {
      width: 100%;
    }
  }

  &__grid {
    margin-bottom: 50px;

    @include media("tb") {
      margin-bottom: 21px;
    }

    @include media("md") {
      display: block;
      margin-bottom: 0;
    }
  }
}

.main__list {
  text-align: center;
  padding: 0;
  margin: 0 0 1rem 0;
  display: grid;
  grid-template-columns: 10rem 10rem 10rem;

  li {
    position: relative;
    transition: border-color 0.3s ease-in-out, width 0.5s ease-in-out,
      height 0.5s ease-in-out;
    position: relative;
    border-bottom: 2px solid #0075ff;
    margin-left: 5px;
    margin-bottom: 0.5rem;

    &::before {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: 0 0;
      transition: 1s;
    }

    &::after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      background: 0 0;
      transition: 1s;
    }

    &::before {
      top: -2px;
      left: 0;
      border-top: 2px solid transparent;
      border-left: 2px solid transparent;
    }

    &::after {
      top: -2px;
      right: -2px;
      border-top: 2px solid transparent;
      border-right: 2px solid transparent;
    }

    &:hover {
      // border-width: 4px;

      &::before,
      &::after {
        border-color: #0075ff;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
      }
    }
  }

